import React from "react";
import {
  Routes,
  Route,
  Navigate,
  HashRouter as Router,
} from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import classNames from "classnames";
import PropTypes from "prop-types";
import Header from "./components/header";
import Sidebar from "./components/sidebar";
import Hardware from "./screens/hardware";
import Cpu from "./screens/cpu";
import Bus from "./screens/bus";
import styles from "./App.module.scss";
import Navigation from "./components/navigation";
import PageNavigation from "./components/page_navigation";

const App = ({ themeName }) => (
  <div className={classNames([styles.app, styles[themeName]])}>
    <Header />

    <Router>
      <Container fluid className={styles.container}>
        <Row>
          <Col
            xl={{ span: 2 }}
            lg={{ span: 2 }}
            md={{ span: 3 }}
            className={styles.fixed}
          >
            <Sidebar>
              <Navigation />
            </Sidebar>
          </Col>

          <Col
            xl={{ span: 8, offset: 2 }}
            lg={{ span: 7, offset: 2 }}
            md={{ span: 9, offset: 3 }}
          >
            <Routes>
              <Route path="/">
                <Route index element={<Hardware />} />
                <Route path="cpu" element={<Cpu />} />
                <Route path="bus" element={<Bus />} />
                <Route path="*" element={<Navigate to="/" />} />
              </Route>
            </Routes>
          </Col>

          <Col
            xl={{ span: 2, offset: 10 }}
            lg={{ span: 3, offset: 9 }}
            className={classNames([styles.fixed, styles["md-close"]])}
          >
            <Sidebar isRight mdClose>
              <PageNavigation />
            </Sidebar>
          </Col>
        </Row>
      </Container>
    </Router>
  </div>
);

App.propTypes = {
  themeName: PropTypes.string.isRequired,
};

export default App;
