import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  isThemeSwitched: false,
};

export const themeSlice = createSlice({
  name: "theme",
  initialState,
  reducers: {
    setIsThemeSwitched: (state, { payload }) => {
      state.isThemeSwitched = payload;
    },
  },
});

export const selectIsThemeSwitched = (state) => state.theme.isThemeSwitched;

export const { setIsThemeSwitched } = themeSlice.actions;

export default themeSlice.reducer;
