import React from "react";
import { useDispatch, useSelector } from "react-redux";
import ThemeToggle from "./ThemeToggle";
import {
  selectIsThemeSwitched,
  setIsThemeSwitched,
} from "../../../store/themeSlice";

const ThemeToggleContainer = () => {
  const isSwitched = useSelector(selectIsThemeSwitched);
  const dispatch = useDispatch();

  const toggleThemeName = () => {
    dispatch(setIsThemeSwitched(!isSwitched));
  };

  return <ThemeToggle isSwitched={isSwitched} onClick={toggleThemeName} />;
};

export default ThemeToggleContainer;
