import React from "react";
import PropTypes from "prop-types";
import { useLocation } from "react-router-dom";
import NavigationHashLink from "./NavigationHashLink";

const NavigationHashLinkContainer = ({ path, hash, children }) => {
  const { hash: locationHash } = useLocation();
  const isActive = hash === decodeURI(locationHash);

  const scrollTo = () => {
    const el = document.getElementById(hash.replace("#", ""));
    el.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <NavigationHashLink
      path={path}
      hash={hash}
      isActive={isActive}
      onClick={scrollTo}
    >
      {children}
    </NavigationHashLink>
  );
};

NavigationHashLinkContainer.propTypes = {
  path: PropTypes.string.isRequired,
  hash: PropTypes.string.isRequired,
  children: PropTypes.string.isRequired,
};

export default NavigationHashLinkContainer;
