import React from "react";
import Heading from "../../components/heading";
import Paragraph from "../../components/paragraph";
import Chapter from "../../components/chapter";
import List from "../../components/list";
import ListItem from "../../components/list_item";
import SubHeading from "../../components/subheading";

const Hardware = () => (
  <div>
    <Heading>Аппаратное обеспечение</Heading>
    <Paragraph>
      Аппара́тное обеспече́ние, аппаратные средства, компьютерные комплектующие,
      «железо», (англ. hardware) — электронные и механические части
      вычислительного устройства, входящие в состав системы или сети, исключая
      программное обеспечение и данные (информацию, которую вычислительная
      система хранит и обрабатывает). Аппаратное обеспечение включает:
      компьютеры и логические устройства, внешние устройства и диагностическую
      аппаратуру, энергетическое оборудование, батареи и аккумуляторы. К
      аппаратному обеспечению относятся устройства, образующие конфигурацию
      компьютера.
    </Paragraph>
    <Paragraph>
      Различают внутренние и внешние устройства. Согласование между отдельными
      узлами и блоками выполняется с помощью аппаратно-логических устройств,
      называемых аппаратными интерфейсами; стандарты на аппаратные интерфейсы
      называют протоколами. Персональный компьютер — универсальная техническая
      система, конфигурацию которой можно изменять по мере необходимости.
    </Paragraph>
    <Chapter>Компьютер</Chapter>
    <Paragraph>
      Аппаратное обеспечение вычислительных систем — обобщённое название
      оборудования, на котором работают компьютеры и сети компьютеров.
    </Paragraph>
    <Paragraph>К аппаратному обеспечению обычно относят:</Paragraph>
    <List>
      <ListItem>центральный процессор (процессоры)</ListItem>
      <ListItem>оперативную память</ListItem>
      <ListItem>системную логику</ListItem>
      <ListItem>периферийные устройства</ListItem>
      <ListItem>сетевое оборудование</ListItem>
    </List>
    <Paragraph>
      Некоторая часть задач, выполняемая аппаратным обеспечением может быть
      выполнена частично или полностью с помощью программной эмуляции, например,
      в персональных компьютерах часто используется программная реализация
      протоколов связи модемов, программная эмуляция функций отрисовки 3D
      изображений. Обычно перенос выполняемой задачи из аппаратной части в
      программную уменьшает стоимость оборудования, но увеличивает нагрузку на
      центральный процессор.
    </Paragraph>
    <Paragraph>
      В случае существенной нагрузки на процессор определённого рода задачами,
      для повышения производительности производят обратную операцию: аппаратно
      реализуют часть алгоритма, уменьшая участие процессора в выполнении
      алгоритма.
    </Paragraph>
    <Paragraph>
      Примеры аппаратной реализации алгоритмов, которые могли бы быть
      реализованы программно:
    </Paragraph>
    <List>
      <ListItem>3D графика</ListItem>
      <ListItem>DMA-режим работы IDE устройств</ListItem>
      <ListItem>FIFO-буффер у COM-портов</ListItem>
      <ListItem>
        Физический процессор для обсчёта поведения объектов в компьютерных играх
      </ListItem>
      <ListItem>
        Математический сопроцессор, ускоряющий операции с плавающей запятой (в
        современных процессорах интегрирован)
      </ListItem>
      <ListItem>Функции файрвола</ListItem>
    </List>
    <SubHeading>Персональный компьютер</SubHeading>
    <Paragraph>
      Обычный персональный компьютер состоит из системного блока, состоящего из
      шасси и периферийных устройств.
    </Paragraph>
    <Paragraph>В состав системного блока входят:</Paragraph>
    <List>
      <ListItem>
        материнская плата с припаянными или установленными на ней:
      </ListItem>
      <List>
        <ListItem>
          Чипсет — взаимосвязанный набор микросхем, логика которого в наибольшей
          степени определяет то, каковы прочие элементы компьютера.
        </ListItem>
        <ListItem>центральный процессор</ListItem>
        <ListItem>
          BIOS — программное обеспечение в ПЗУ материнской платы.
        </ListItem>
        <ListItem>
          кулер охлаждения процессора как основного тепловыделяющего элемента
          современного компьютера.
        </ListItem>
        <ListItem>оперативная память и кэш</ListItem>
        <ListItem>
          слоты расширения шин — PCI, PCI-E, USB, FireWire, AGP (устарела), ISA
          (устарела), EISA (устарела)
        </ListItem>
        <ListItem>
          контроллеры устройств — хранения: IDE, SCSI, SATA, SAS или других
          типов, находящиеся непосредственно на материнской плате (встроенные)
          либо на платах расширения.
        </ListItem>
        <ListItem>
          видеоконтроллер (встроенный или в виде отдельной платы), передающий
          сигнал на монитор
        </ListItem>
        <ListItem>звуковой контроллер</ListItem>
        <ListItem>сетевой интерфейс (сетевая плата)</ListItem>
      </List>
      <ListItem>блок питания</ListItem>
      <ListItem>
        система охлаждения — дополнительные элементы охлаждения (вентиляторы,
        механические элементы, части водяной системы охлаждения), необходимые
        при установке блоков, имеющих повышенное тепловыделение (видеокарт,
        вычислительных сопроцессоров).
      </ListItem>
    </List>
    <Paragraph>
      Через контроллеры к материнской плате при помощи шлейфов кабелей,
      сигнальных и питания, подключаются находящиеся внутри системного блока
      возможные элементы:
    </Paragraph>
    <List>
      <ListItem>
        жёсткий диск (их может быть несколько, они могут быть объединены в
        RAID-массив)
      </ListItem>
      <ListItem>SSD</ListItem>
      <ListItem>накопитель на гибких дисках</ListItem>
      <ListItem>накопитель на гибких дисках</ListItem>
    </List>
    <Paragraph>
      Кроме того, в аппаратное обеспечение компьютера также входят внешние (по
      отношению к системному блоку) компоненты — периферийные устройства в
      различных сочетаниях:
    </Paragraph>
    <List>
      <ListItem>Устройства ввода</ListItem>
      <List>
        <ListItem>клавиатура</ListItem>
        <ListItem>мышь, трекбол или тачпад</ListItem>
        <ListItem>графический планшет</ListItem>
        <ListItem>джойстик</ListItem>
        <ListItem>сканер</ListItem>
        <ListItem>микрофон</ListItem>
      </List>
      <ListItem>устройства вывода</ListItem>
      <List>
        <ListItem>Монитор</ListItem>
        <ListItem>колонки/наушники</ListItem>
        <ListItem>печатающие устройства типа принтера или плоттера</ListItem>
      </List>
      <ListItem>
        для связи используются различные модемы и сетевое оборудование:
        маршрутизатор, сетевой коммутатор, беспроводная точка доступа.
      </ListItem>
      <ListItem>
        устройства ввода-вывода для технологий виртуальной реальности.
      </ListItem>
    </List>
    <Chapter>Архитектура</Chapter>
    <Paragraph>
      Под архитектурой аппаратного обеспечения компьютера понимаются внутренние
      компоненты компьютера и подключенные к нему устройства ввода (как
      компьютерная мышь и клавиатура) и устройства вывода (монитор). Внутренние
      компоненты компьютера вместе представляют собой вычислительное и
      управляющее устройство, объединённое шиной. Более распространены
      обозначения процессор, оперативная память и жесткий диск. Шина соединяет
      отдельные компоненты в сложную систему, так как без шины разрозненные
      детали не смогли бы функционировать. Процессор или ЦПУ определяет основную
      скорость работы компьютера. Требования к тактированию ЦПУ и собственной
      мощности процессора постоянно увеличиваются, так как и предъявляемые к
      технике требования тоже растут. Программное обеспечение, которое позже
      устанавливается на компьютер, требует все большей мощности процессора.
    </Paragraph>
    <Paragraph>
      Термин архитектура распространяется на устройство комплектной системы, так
      как отдельные компоненты должны быть друг с другом согласованы, чтобы
      гарантировать бесперебойное протекание процесса. В противном случае в
      будущем это приведёт к проблемам с компьютером. Если элемент не подходит к
      другим компонентам, то они так же не могут потреблять полную мощность.
      Отдельные составные блоки, такие, как процессор, оперативная память или
      шина, при сборке корпуса должны составлять одно целое. Иначе подключенное
      к процессору программное обеспечение не сможет выполнять свои задачи в
      полной мере и обращение с компьютером будет усложнено.
    </Paragraph>
  </div>
);

export default Hardware;
