import React from "react";
import Heading from "../../components/heading";
import Paragraph from "../../components/paragraph";
import Chapter from "../../components/chapter";
import List from "../../components/list";
import ListItem from "../../components/list_item";
import SubHeading from "../../components/subheading";

const Cpu = () => (
  <div>
    <Heading>Процессор</Heading>
    <Paragraph>
      Центра́льный проце́ссор (ЦП; также центра́льное проце́ссорное устро́йство —
      ЦПУ; англ. central processing unit, CPU, дословно — центральное
      обрабатывающее устройство, часто просто процессор) — электронный блок либо
      интегральная схема, исполняющая машинные инструкции (код программ),
      главная часть аппаратного обеспечения компьютера или программируемого
      логического контроллера. Иногда называют микропроцессором или просто
      процессором.
    </Paragraph>
    <Paragraph>
      значально термин центральное процессорное устройство описывал
      специализированный класс логических машин, предназначенных для выполнения
      сложных компьютерных программ. Вследствие довольно точного соответствия
      этого назначения функциям существовавших в то время компьютерных
      процессоров он естественным образом был перенесён на сами компьютеры.
      Начало применения термина и его аббревиатуры по отношению к компьютерным
      системам было положено в 1960-е годы. Устройство, архитектура и реализация
      процессоров с тех пор неоднократно менялись, однако их основные
      исполняемые функции остались теми же, что и прежде.
    </Paragraph>
    <Paragraph>
      Главными характеристиками ЦПУ являются: тактовая частота,
      производительность, энергопотребление, нормы литографического процесса,
      используемого при производстве (для микропроцессоров), и архитектура.
    </Paragraph>
    <Paragraph>
      Ранние ЦП создавались в виде уникальных составных частей для уникальных и
      даже единственных в своём роде компьютерных систем. Позднее от
      дорогостоящего способа разработки процессоров, предназначенных для
      выполнения одной единственной или нескольких узкоспециализированных
      программ, производители компьютеров перешли к серийному изготовлению
      типовых классов многоцелевых процессорных устройств. Тенденция к
      стандартизации компьютерных комплектующих зародилась в эпоху бурного
      развития полупроводниковых элементов, мейнфреймов и мини-компьютеров, а с
      появлением интегральных схем она стала ещё более популярной. Создание
      микросхем позволило ещё больше увеличить сложность ЦП с одновременным
      уменьшением их физических размеров. Стандартизация и миниатюризация
      процессоров привели к глубокому проникновению основанных на них цифровых
      устройств в повседневную жизнь человека. Современные процессоры можно
      найти не только в таких высокотехнологичных устройствах, как компьютеры,
      но и в автомобилях, калькуляторах, мобильных телефонах и даже в детских
      игрушках. Чаще всего они представлены микроконтроллерами, где, помимо
      вычислительного устройства, на кристалле расположены дополнительные
      компоненты (память программ и данных, интерфейсы, порты ввода-вывода,
      таймеры и др.). Современные вычислительные возможности микроконтроллера
      сравнимы с процессорами персональных ЭВМ десятилетней давности, а чаще
      даже значительно превосходят их показатели.
    </Paragraph>
    <Chapter>Архитектура фон Неймана</Chapter>
    <Paragraph>
      Большинство современных процессоров для персональных компьютеров в общем
      основано на той или иной версии циклического процесса последовательной
      обработки данных, описанного Джоном фон Нейманом.
    </Paragraph>
    <Paragraph>
      В июле 1946 года Бёрксом, Голдстайном и фон Нейманом была написана
      знаменитая монография под названием «Предварительное рассмотрение
      логического устройства электронного вычислительного прибора», которая
      подробно описала устройство и технические характеристики будущего
      компьютера, которые позднее стали носить название «архитектура фон
      Неймана». Эта работа развивала идеи, изложенные фон Нейманом в мае 1945
      года в рукописи под названием «Первый проект отчёта о EDVAC».
    </Paragraph>
    <Paragraph>
      Отличительной особенностью архитектуры фон Неймана является то, что
      инструкции и данные хранятся в одной и той же памяти.
    </Paragraph>
    <Paragraph>
      В различных архитектурах и для различных команд могут потребоваться
      дополнительные этапы. Например, для арифметических команд могут
      потребоваться дополнительные обращения к памяти, во время которых
      производится считывание операндов и запись результатов.
    </Paragraph>
    <Paragraph>Этапы цикла выполнения:</Paragraph>
    <List isDecimal>
      <ListItem>
        Процессор выставляет число, хранящееся в регистре счётчика команд, на
        шину адреса и отдаёт памяти команду чтения.
      </ListItem>
      <ListItem>
        Выставленное число является для памяти адресом; память, получив адрес и
        команду чтения, выставляет содержимое, хранящееся по этому адресу, на
        шину данных и сообщает о готовности.
      </ListItem>
      <ListItem>
        Процессор получает число с шины данных, интерпретирует его как команду
        (машинную инструкцию) из своей системы команд и исполняет её.
      </ListItem>
      <ListItem>
        Если последняя команда не является командой перехода, процессор
        увеличивает на единицу (в предположении, что длина каждой команды равна
        единице) число, хранящееся в счётчике команд; в результате там
        образуется адрес следующей команды.
      </ListItem>
    </List>
    <Paragraph>
      Данный цикл выполняется неизменно, и именно он называется процессом
      (откуда и произошло название устройства).
    </Paragraph>
    <Paragraph>
      Во время процесса процессор считывает последовательность команд,
      содержащихся в памяти, и исполняет их. Такая последовательность команд
      называется программой и представляет алгоритм работы процессора.
      Очерёдность считывания команд изменяется в случае, если процессор
      считывает команду перехода, — тогда адрес следующей команды может
      оказаться другим. Другим примером изменения процесса может служить случай
      получения команды останова или переключение в режим обработки прерывания.
    </Paragraph>
    <Paragraph>
      Команды центрального процессора являются самым нижним уровнем управления
      компьютером, поэтому выполнение каждой команды неизбежно и безусловно. Не
      производится никакой проверки на допустимость выполняемых действий, в
      частности, не проверяется возможная потеря ценных данных. Чтобы компьютер
      выполнял только допустимые действия, команды должны быть соответствующим
      образом организованы в виде необходимой программы.
    </Paragraph>
    <Paragraph>
      Скорость перехода от одного этапа цикла к другому определяется тактовым
      генератором. Тактовый генератор вырабатывает импульсы, служащие ритмом для
      центрального процессора. Частота тактовых импульсов называется тактовой
      частотой.
    </Paragraph>
    <SubHeading>Конвейерная архитектура</SubHeading>
    <Paragraph>
      Конвейерная архитектура (англ. pipelining) была введена в центральный
      процессор с целью повышения быстродействия. Обычно для выполнения каждой
      команды требуется осуществить некоторое количество однотипных операций,
      например: выборка команды из ОЗУ, дешифровка команды, адресация операнда в
      ОЗУ, выборка операнда из ОЗУ, выполнение команды, запись результата в ОЗУ.
      Каждую из этих операций сопоставляют одной ступени конвейера. Например,
      конвейер микропроцессора с архитектурой MIPS-I содержит четыре стадии:
    </Paragraph>
    <List isDecimal>
      <ListItem>получение и декодирование инструкции</ListItem>
      <ListItem>адресация и выборка операнда из ОЗУ</ListItem>
      <ListItem>выполнение арифметических операций</ListItem>
      <ListItem>сохранение результата операции</ListItem>
    </List>
    <Paragraph>
      После освобождения k-й ступени конвейера она сразу приступает к работе над
      следующей командой. Если предположить, что каждая ступень конвейера тратит
      единицу времени на свою работу, то выполнение команды на конвейере длиной
      в n ступеней займёт n единиц времени, однако в самом оптимистичном случае
      результат выполнения каждой следующей команды будет получаться через
      каждую единицу времени.
    </Paragraph>
    <Paragraph>
      Действительно, при отсутствии конвейера выполнение команды займёт n единиц
      времени (так как для выполнения команды по-прежнему необходимо выполнять
      выборку, дешифровку и т. д.), и для исполнения m команд понадобится n * m
      единиц времени; при использовании конвейера (в самом оптимистичном случае)
      для выполнения m команд понадобится всего лишь n+m единиц времени.
    </Paragraph>
    <Paragraph>Факторы, снижающие эффективность конвейера:</Paragraph>
    <List isDecimal>
      <ListItem>
        Простой конвейера, когда некоторые ступени не используются (например,
        адресация и выборка операнда из ОЗУ не нужны, если команда работает с
        регистрами).
      </ListItem>
      <ListItem>
        Ожидание: если следующая команда использует результат предыдущей, то
        последняя не может начать выполняться до выполнения первой (это
        преодолевается при использовании внеочередного выполнения команд —
        out-of-order execution).
      </ListItem>
      <ListItem>
        Очистка конвейера при попадании в него команды перехода (эту проблему
        удаётся сгладить, используя предсказание переходов).
      </ListItem>
    </List>
    <Paragraph>
      Некоторые современные процессоры имеют более 30 ступеней в конвейере, что
      повышает производительность процессора, но, однако, приводит к увеличению
      длительности простоя (например, в случае ошибки в предсказании условного
      перехода). Не существует единого мнения по поводу оптимальной длины
      конвейера: различные программы могут иметь существенно различные
      требования.
    </Paragraph>
    <SubHeading>Суперскалярная архитектура</SubHeading>
    <Paragraph>
      Способность выполнения нескольких машинных инструкций за один такт
      процессора путём увеличения числа исполнительных устройств. Появление этой
      технологии привело к существенному увеличению производительности, в то же
      время существует определённый предел роста числа исполнительных устройств,
      при превышении которого производительность практически перестает расти, а
      исполнительные устройства простаивают. Частичным решением этой проблемы
      является, например, технология Hyper-threading.
    </Paragraph>
    <SubHeading>CISC-процессоры</SubHeading>
    <Paragraph>
      Complex instruction set computer — вычисления со сложным набором команд.
      Процессорная архитектура, основанная на усложнённом наборе команд.
      Типичными представителями CISC являются микропроцессоры семейства x86
      (хотя уже много лет эти процессоры являются CISC только по внешней системе
      команд: в начале процесса исполнения сложные команды разбиваются на более
      простые микрооперации (МОП), исполняемые RISC-ядром).
    </Paragraph>
    <SubHeading>RISC-процессоры</SubHeading>
    <Paragraph>
      Reduced instruction set computer — вычисления с упрощённым набором команд
      (в литературе слово reduced нередко ошибочно переводят как «сокращённый»).
      Архитектура процессоров, построенная на основе упрощённого набора команд,
      характеризуется наличием команд фиксированной длины, большого количества
      регистров, операций типа регистр-регистр, а также отсутствием косвенной
      адресации. Концепция RISC разработана Джоном Коком из IBM Research,
      название придумано Дэвидом Паттерсоном (David Patterson).
    </Paragraph>
    <Paragraph>
      Упрощение набора команд призвано сократить конвейер, что позволяет
      избежать задержек на операциях условных и безусловных переходов.
      Однородный набор регистров упрощает работу компилятора при оптимизации
      исполняемого программного кода. Кроме того, RISC-процессоры отличаются
      меньшим энергопотреблением и тепловыделением.
    </Paragraph>
    <Paragraph>
      Среди первых реализаций этой архитектуры были процессоры MIPS, PowerPC,
      SPARC, Alpha, PA-RISC. В мобильных устройствах широко используются
      ARM-процессоры.
    </Paragraph>
    <SubHeading>MISC-процессоры</SubHeading>
    <Paragraph>
      Minimum instruction set computer — вычисления с минимальным набором
      команд. Дальнейшее развитие идей команды Чака Мура, который полагает, что
      принцип простоты, изначальный для RISC-процессоров, слишком быстро отошёл
      на задний план. В пылу борьбы за максимальное быстродействие, RISC догнал
      и обогнал многие CISC-процессоры по сложности. Архитектура MISC строится
      на стековой вычислительной модели с ограниченным числом команд (примерно
      20—30 команд).
    </Paragraph>
    <SubHeading>VLIW-процессоры</SubHeading>
    <Paragraph>
      Very long instruction word — сверхдлинное командное слово. Архитектура
      процессоров с явно выраженным параллелизмом вычислений, заложенным в
      систему команд процессора. Являются основой для архитектуры EPIC. Ключевым
      отличием от суперскалярных CISC-процессоров является то, что для них
      загрузкой исполнительных устройств занимается часть процессора
      (планировщик), на что отводится достаточно малое время, в то время как
      загрузкой вычислительных устройств для VLIW-процессора занимается
      компилятор, на что отводится существенно больше времени (качество загрузки
      и, соответственно, производительность теоретически должны быть выше).
    </Paragraph>
    <Paragraph>
      Например, Intel Itanium, Transmeta Crusoe, Efficeon и Эльбрус.
    </Paragraph>
    <SubHeading>Многоядерные процессоры</SubHeading>
    <Paragraph>
      Содержат несколько процессорных ядер в одном корпусе (на одном или
      нескольких кристаллах).
    </Paragraph>
    <Paragraph>
      Процессоры, предназначенные для работы одной копии операционной системы на
      нескольких ядрах, представляют собой высокоинтегрированную реализацию
      мультипроцессорности.
    </Paragraph>
    <SubHeading>Кэширование</SubHeading>
    <Paragraph>
      Кэширование — это использование дополнительной быстродействующей памяти
      (так называемого кэша — англ. cache, от фр. cacher — «прятать») для
      хранения копий блоков информации из основной (оперативной) памяти,
      вероятность обращения к которым в ближайшее время велика.
    </Paragraph>
    <Paragraph>
      Различают кэши 1-, 2- и 3-го уровней (обозначаются L1, L2 и L3 — от Level
      1, Level 2 и Level 3). Кэш 1-го уровня имеет наименьшую латентность (время
      доступа), но малый размер, кроме того, кэши первого уровня часто делаются
      многопортовыми. Так, процессоры AMD K8 умели производить одновременно
      64-битные запись и чтение, либо два 64-битных чтения за такт, AMD K8L
      может производить два 128-битных чтения или записи в любой комбинации.
      Процессоры Intel Core 2 могут производить 128-битные запись и чтение за
      такт. Кэш 2-го уровня обычно имеет значительно большую латентность
      доступа, но его можно сделать значительно больше по размеру. Кэш 3-го
      уровня — самый большой по объёму и довольно медленный, но всё же он
      гораздо быстрее, чем оперативная память.
    </Paragraph>
    <Chapter>Гарвардская архитектура</Chapter>
    <Paragraph>
      Гарвардская архитектура отличается от архитектуры фон Неймана тем, что
      программный код и данные хранятся в разной памяти. В такой архитектуре
      невозможны многие методы программирования (например, программа не может во
      время выполнения менять свой код; невозможно динамически перераспределять
      память между программным кодом и данными); зато гарвардская архитектура
      позволяет более эффективно выполнять работу в случае ограниченных
      ресурсов, поэтому она часто применяется во встраиваемых системах.
    </Paragraph>
    <Chapter>Параллельная архитектура</Chapter>
    <Paragraph>
      Архитектура фон Неймана обладает тем недостатком, что она
      последовательная. Какой бы огромный массив данных ни требовалось
      обработать, каждый его байт должен будет пройти через центральный
      процессор, даже если над всеми байтами требуется провести одну и ту же
      операцию. Этот эффект называется узким горлышком фон Неймана.
    </Paragraph>
    <Paragraph>
      Для преодоления этого недостатка предлагались и предлагаются архитектуры
      процессоров, которые называются параллельными. Параллельные процессоры
      используются в суперкомпьютерах.
    </Paragraph>
    <Paragraph>
      Возможными вариантами параллельной архитектуры могут служить (по
      классификации Флинна):
    </Paragraph>
    <List>
      <ListItem>SISD — один поток команд, один поток данных</ListItem>
      <ListItem>SIMD — один поток команд, много потоков данных</ListItem>
      <ListItem>MISD — много потоков команд, один поток данных</ListItem>
      <ListItem>MIMD — много потоков команд, много потоков данных</ListItem>
    </List>
    <SubHeading>Цифровые сигнальные процессоры</SubHeading>
    <Paragraph>
      Для цифровой обработки сигналов, особенно при ограниченном времени
      обработки, применяют специализированные высокопроизводительные сигнальные
      микропроцессоры (англ. digital signal processor, DSP) с параллельной
      архитектурой.
    </Paragraph>
  </div>
);

export default Cpu;
