import React from "react";
import PropTypes from "prop-types";
import NavigationHashLink from "./navigation_hash_link";

const PageNavigation = ({ chapters, path }) => (
  <nav>
    {chapters.map(({ heading, hash }) => (
      <NavigationHashLink key={hash} path={path} hash={hash}>
        {heading}
      </NavigationHashLink>
    ))}
  </nav>
);

PageNavigation.propTypes = {
  chapters: PropTypes.arrayOf(
    PropTypes.shape({
      heading: PropTypes.string,
      hash: PropTypes.string,
    })
  ).isRequired,
  path: PropTypes.string.isRequired,
};

export default PageNavigation;
