import { createSelector, createSlice } from "@reduxjs/toolkit";
import chapterHeadingToID from "../helpers/chapterHeadingToID";

const initialState = {
  screens: [
    {
      path: "/",
      heading: "Главная",
      chapters: [
        {
          heading: "Компьютер",
        },
        {
          heading: "Архитектура",
        },
      ],
    },
    {
      path: "/cpu",
      heading: "Процессор",
      chapters: [
        {
          heading: "Архитектура фон Неймана",
        },
        {
          heading: "Гарвардская архитектура",
        },
        {
          heading: "Параллельная архитектура",
        },
      ],
    },
    {
      path: "/bus",
      heading: "Шины",
      chapters: [
        {
          heading: "Описание шин",
        },
        {
          heading: "Примеры внутренних компьютерных шин",
        },
        {
          heading: "Примеры внешних компьютерных шин",
        },
      ],
    },
  ],
};

export const screensSlice = createSlice({
  name: "screens",
  initialState,
  reducers: {},
});

export const selectScreens = createSelector(
  (state) => state.screens.screens,
  (screens) =>
    screens.map((screen) => {
      const { chapters } = screen;

      const chaptersMapped = chapters.map(({ heading }) => ({
        heading,
        hash: `#${chapterHeadingToID(heading)}`,
      }));

      return {
        ...screen,
        chapters: chaptersMapped,
      };
    })
);

export default screensSlice.reducer;
