import React from "react";
import { useSelector } from "react-redux";
import App from "./App";
import { selectIsThemeSwitched } from "./store/themeSlice";

const AppContainer = () => {
  const isThemeSwitched = useSelector(selectIsThemeSwitched);

  return <App themeName={isThemeSwitched ? "dark" : ""} />;
};

export default AppContainer;
