import React from "react";
import { useLocation } from "react-router-dom";
import { useSelector } from "react-redux";
import PropTypes from "prop-types";
import PageNavigation from "./PageNavigation";
import { selectScreens } from "../../store/screensSlice";

const PageNavigationContainer = ({ isDecimal }) => {
  const { pathname } = useLocation();

  const screens = useSelector(selectScreens);
  const screen = screens.find(({ path }) => path === pathname);

  const chapters = screen?.chapters || [];

  return (
    <PageNavigation path={pathname} chapters={chapters} isDecimal={isDecimal} />
  );
};

PageNavigationContainer.defaultProps = {
  isDecimal: false,
};

PageNavigationContainer.propTypes = {
  isDecimal: PropTypes.bool,
};

export default PageNavigationContainer;
